$btn-size: 4.0104vw;
.all-character {
    display: grid;
    grid-template-columns: 18.5937vw 81.4063vw;
    height: 56.25%;
    &__nav {
        width: 18.5937vw;
        border-bottom: 2px solid #ece9e1;
    }
    &__btn-link {
        font-size: 0;
        display: block;
        width: 18.5937vw;
        height: $btn-size;
        background: #fff url('../images/nav-bg-all@2x.jpg') no-repeat 0 0;
        background-size: 37.1875vw auto;
        transition: all 300ms ease;
        &.link-cht-02 {
            background-position-y: 0 -$btn-size * 1;
        }
        &.link-cht-03 {
            background-position-y: 0 -$btn-size * 2;
        }
        &.link-cht-04 {
            background-position-y: 0 -$btn-size * 3;
        }
        &.link-cht-05 {
            background-position-y: 0 -$btn-size * 4;
        }
        &.link-cht-06 {
            background-position-y: 0 -$btn-size * 5;
        }
        &.link-cht-07 {
            background-position-y: 0 -$btn-size * 6;
        }
        &.link-cht-08 {
            background-position-y: 0 -$btn-size * 7;
        }
        &.link-cht-09 {
            background-position-y: 0 -$btn-size * 8;
        }
        &.link-cht-10 {
            background-position-y: 0 -$btn-size * 9;
        }
        &.link-cht-11 {
            background-position-y: 0 -$btn-size * 10;
        }
        &.link-cht-12 {
            background-position-y: 0 -$btn-size * 11;
        }
        &.link-cht-13 {
            background-position-y: 0 -$btn-size * 12;
        }
        &.link-cht-14 {
            background-position-y: 0 -$btn-size * 13;
        }
        &:hover {
            background-position-x: -18.5937vw;
        }
        &.active {
            pointer-events: none;
            background-position-x: -18.5937vw;
        }
        &.disable {
            pointer-events: none;
        }
    }
    &__content {
        position: relative;
        &::after {
            position: absolute;
            content: "";
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            transition: all 300ms ease;
            background-color: #fff;
            pointer-events: none;
            z-index: 100;
            opacity: 0;
        }
        &.load {
            &::after {
                opacity: 1;
            }
        }
    }
}