.intro {
    width: 100%;
    height: 0;
    padding-bottom: 56.25%;
    background-color: #000;
    position: absolute;
    z-index: 200;
    left: 0;
    transition: all 1000ms ease;
    &.end {
        pointer-events: none;
        opacity: 0;
    }
    &__video {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
    }
}