.cht-02-text {
    @extend .page-text;
    width: 23.9583vw;
    height: 16.6666vw;
    &::-webkit-scrollbar-track {
        background-color: #523879;
        border-radius: 100px;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #f2b3ff;
    }
}

.cht-02-btn-toggle {
    @extend .cht-btn-toggle;
    background-color: #a844d2;
    &:hover {
        background-color: lighten(#a844d2, 10%);
    }
    &.active {
        background-color: #5e2487;
    }
}

.cht-content-02 {
    &__status {
        position: relative;
        background-color: #7e30a0;
        height: 100%;
    }
    &__nav {
        position: relative;
        height: 100%;
        background-color: #7e30a0;
    }
    &__slash-top {
        position: absolute;
        top: 19vw;
        left: 0;
        width: 100%;
        height: 19.2708vw;
        border-top: solid 2px #f2b3ff;
        border-bottom: solid 2px #f2b3ff;
        z-index: 100;
        transform: skewY(-21.5deg);
        pointer-events: none;
    }
    &__slash-bottom {
        position: absolute;
        top: 42.083vw;
        left: 0;
        width: 100%;
        height: 13.9583vw;
        border-top: solid 0.10416vw #f2b3ff;
        z-index: 100;
        transform: skewY(-21.5deg);
        pointer-events: none;
    }
    &__btn-cht-1-1 {
        display: block;
        background: transparent url('../images/cht02-nav-1.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;
        height: 22.9166vw;
        position: absolute;
        z-index: 30;
        top: 0;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
    }
    &__btn-cht-1-2 {
        display: block;
        background: transparent url('../images/cht02-nav-2.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;
        height: 26.875vw;
        position: absolute;
        z-index: 20;
        top: 15.4687vw;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
    }
    &__btn-cht-1-3 {
        display: block;
        background: transparent url('../images/cht02-nav-3.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;
        height: 21.4583vw;
        position: absolute;
        z-index: 10;
        top: 34.8437vw;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
    }
}


.cht-02-status-0 {
    @extend .cht-status-block;
    &__status-1 {
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        height: 100%;
        background: #7e30a0 url('../images/cht02-0.jpg') no-repeat right top;
        background-size: cover;
    }
    &__text {
        @extend .page-text;
        transform: translate3d(0,-30%,0);
        width: 23.9583vw;
        height: 23.9583vw;
        &::-webkit-scrollbar-track {
            background-color: #523879;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #f2b3ff;
        }
    }
}

.cht-02-status-1 {
    @extend .cht-status-block;
    &__text {
        @extend .cht-02-text;
    }
    &__status {
        @extend .cht-status;
        &.active {
            opacity: 1;
            z-index: 100;
        }
        &.status-1 {
            background: #7e30a0 url('../images/cht02-1-1.jpg') no-repeat right top;
            background-size: cover;
        }
        &.status-2 {
            background: #7e30a0 url('../images/cht02-1-2.jpg') no-repeat right top;
            background-size: cover;
        }
        &.status-3 {
            background: #7e30a0 url('../images/cht02-1-3.jpg') no-repeat right top;
            background-size: cover;
        }
    }
    &__nav {
        @extend .toggle-nav;
    }
    &__btn-toggle {
        @extend .cht-02-btn-toggle;
    }
}

