.toggle-nav {
    position: absolute;
    top: 50%;
    right: 1.0416vw;
    transform: translate3d(0,420%,0);
    z-index: 200;
    display: flex;
    gap: 0.8333vw;
}

.cht-status-block {
    position: absolute;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 0;
    transition: all 1000ms ease;
    pointer-events: none;
    &.active {
        pointer-events: auto;
        opacity: 1;
        z-index: 100;
    }
}

.cht-status {
    position: absolute;
    width: 100%;
    left: 0;
    top: 0;
    height: 100%;
    opacity: 0;
    z-index: 0;
    transition: all 1000ms ease;
    &.active {
        opacity: 1;
        z-index: 100;
    }
}

.page-text {
    box-sizing: border-box;
    padding-right: 0.4166vw;
    position: absolute;
    right: 3.125vw;
    top: 50%;
    transform: translate3d(0,-50%,0);
    overflow-y: auto;
    &::-webkit-scrollbar {
        width: 0.2083vw;
    }
    &::-webkit-scrollbar-track {
        border-radius: 100px;
    }
    &::-webkit-scrollbar-thumb {
        border-radius: 0.2083vw;
    }
    p {
        font-size: 0.9375vw;
        color: #fff;
        line-height: 1.8;
        &:not(:last-child) {
            margin-bottom: 1.5625vw;
        }
    }
}

.cht-btn-toggle {
    position: relative;
    box-shadow: 0 3px 3px 0 rgba(0,0,0,0.2);
    transition: all 300ms ease;
    box-sizing: border-box;
    border: 0;
    padding: 0;
    width: 2.9166vw;
    height: 2.7083vw;
    &::after {
        content: "轉";
        position: absolute;
        right: 0.2083vw;
        bottom: 0;
        font-size: 1.1458vw;
        color: #fff;
        text-shadow: 0 1px 2px rgba(0,0,0,0.2);
        transition: all 300ms ease;
    }
    span {
        font-size: 1.9791vw;
        color: #fff;
        font-weight: 900;
        position: absolute;
        left: 0.3125vw;
        top: -0.625vw;
        text-shadow: 0 2px 4px rgba(0,0,0,0.2);
        transition: all 300ms ease;
    }
    &.active {
        pointer-events: none;
        box-shadow: 0 3px 3px 0 rgba(0,0,0,0), inset 0 2px 4px rgba(0,0,0,0.4);
        span {
            color: #f8d8da;
        }
        &::after {
            color: #f8d8da;
        }
    }
}