.cht-content {
    display: grid;
    grid-template-columns: 1fr 18.75vw;
    height: 100%;
    &__main {
        position: relative;
    }
}

.cht-01-text {
    @extend .page-text;
    width: 23.9583vw;
    height: 16.6666vw;
    &::-webkit-scrollbar-track {
        background-color: #79383c;
    }
    &::-webkit-scrollbar-thumb {
        background-color: #ffc7cb;
    }
}

.cht-01-btn-toggle {
    @extend .cht-btn-toggle;
    background-color: #c83b46;
    &:hover {
        background-color: lighten(#c83b46, 10%);
    }
    &.active {
        background-color: #87242b;
    }
}

.cht-content-01 {
    &__status {
        position: relative;
        background-color: #aa2f38;
        height: 100%;
    }
    &__nav {
        position: relative;
        height: 100%;
        background-color: #a83039;
    }
    &__slash-top {
        position: absolute;
        top: 13.9062vw;
        left: 0;
        width: 100%;
        height: 13.9583vw;
        border-top: solid 0.10416vw #ffb3b3;
        border-bottom: solid 0.10416vw #ffb3b3;
        z-index: 100;
        transform: skewY(-21.5deg);
        pointer-events: none;
    }
    &__slash-bottom {
        position: absolute;
        top: 42.0833vw;
        left: 0;
        width: 100%;
        height: 13.9583;
        border-top: solid 0.10416vw #ffb3b3;
        z-index: 100;
        transform: skewY(-21.5deg);
        pointer-events: none;
    }
    &__btn-cht-1-1 {
        display: block;
        background: transparent url('../images/cht01-nav-1.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;
        height: 17.6562vw;
        position: absolute;
        z-index: 30;
        top: 0;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
    }
    &__btn-cht-1-2 {
        display: block;
        background: transparent url('../images/cht01-nav-2.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;
        height: 21.5104vw;
        position: absolute;
        z-index: 20;
        top: 10.2604vw;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
    }
    &__btn-cht-1-3 {
        display: block;
        background: transparent url('../images/cht01-nav-3.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;
        height: 21.5104vw;
        position: absolute;
        z-index: 10;
        top: 24.3229vw;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
    }
    &__btn-cht-1-4 {
        display: block;
        background: transparent url('../images/cht01-nav-4.png') no-repeat 0 0;
        background-size: 37.5vw auto;
        width: 18.75vw;;
        height: 17.9166vw;
        position: absolute;
        z-index: 10;
        top: 38.3854vw;
        left: 0;
        font-size: 0;
        &:hover {
            background-position-x: -18.75vw;
        }
        &.active {
            background-position-x: -18.75vw;
        }
        &.comming {
            pointer-events: none;
        }
    }
}

.cht-01-status-0 {
    @extend .cht-status-block;
    &__status-1 {
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        height: 100%;
        background: #a83039 url('../images/cht01-0.jpg') no-repeat right top;
        background-size: cover;
    }
    &__text {
        @extend .page-text;
        transform: translate3d(0,-30%,0);
        width: 23.9583vw;
        height: 23.9583vw;
        &::-webkit-scrollbar-track {
            background-color: #79383c;
        }
        &::-webkit-scrollbar-thumb {
            background-color: #ffc7cb;
        }
    }
}

.cht-01-status-1 {
    @extend .cht-status-block;
    &__text {
        @extend .cht-01-text;
    }
    &__status {
        @extend .cht-status;
        &.status-1 {
            background: #a83039 url('../images/cht01-1-1.jpg') no-repeat right top;
            background-size: cover;
        }
        &.status-2 {
            background: #a83039 url('../images/cht01-1-2.jpg') no-repeat right top;
            background-size: cover;
        }
        &.status-3 {
            background: #a83039 url('../images/cht01-1-3.jpg') no-repeat right top;
            background-size: cover;
        }
    }
    &__nav {
        @extend .toggle-nav;
    }
    &__btn-toggle {
        @extend .cht-01-btn-toggle;
    }
}

