html {
    font-family: 'Kanit', sans-serif;
    width: 100%;
    height: 100%;
    -webkit-font-smoothing: antialiased;
}

body {
    width: 100%;
    height: 100%;
    background-color: var(--bg-color);
}

@mixin rsp-img {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
}

.container {
    @include wrap;
}

.wrap {
    max-width: 1920px;
    margin: 0 auto;
    overflow: hidden;
}