.kv {
    position: relative;
    overflow: hidden;
    padding-bottom: 56.25%;
    height: 0;
    &__slide {
        position: absolute;
        width: 100%;
        left: 0;
        top: 0;
        z-index: 10;
    }
    &__img {
        max-width: 100%;
        display: block;
        margin: 0;
        transition: all 6000ms ease-out;
        transform-origin: 50% bottom;
    }
    .swiper-slide-active img {
        transform: scale(1.05);
    }
    &__logo-wrap {
        z-index: 20;
        position: absolute;
        width: 62.5vw;
        left: 50%;
        z-index: 100;
        top: 1.041vw;
        transform: translate3d(-50%,0,0);
    }
    &__logo {
        width: 7.968vw;
        height: 3.593vw;
        a {
            display: block;
        }
        img {
            max-width: 100%;
            display: block;
        }
    }
    &__nav {
        z-index: 30;
        position: absolute;
        width: 62.5vw;
        left: 50%;
        bottom: 10.291vw;
        transform: translate3d(-50%,0,0);
        display: flex;
        justify-content: center;
        z-index: 100;
        gap: 7.291vw;
    }
    &__btn-download {
        font-size: 0;
        display: block;
        width: 16.6666vw;
        height: 7.0833vw;
        background: transparent url('../images/btn-download-off@2x.png') no-repeat 0 0;
        background-size: 33.3333vw auto;
        &:hover {
            background-position-x: -16.6666vw;
        }
    }
    &__btn-signup {
        font-size: 0;
        display: block;
        width: 16.6666vw;
        height: 7.0833vw;
        background: transparent url('../images/btn-signup-off@2x.png') no-repeat 0 0;
        background-size: 33.3333vw auto;
        &:hover {
            background-position-x: -16.6666vw;
        }
    }
}